'use client'

import { useState } from 'react'
import { useForm } from 'react-hook-form'
import { z } from 'zod'
import { zodResolver } from '@hookform/resolvers/zod'
import { toast } from 'sonner'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { api } from '@/lib/axios'
import { useRouter } from 'next/navigation'

const addUserFormSchema = z.object({
  name: z.string().min(3, 'Nome é obrigatório'),
  email: z.string().email('Email inválido'),
  type: z.string().min(1, 'Tipo é obrigatório'),
  password: z.string().min(4, 'Senha deve ter pelo menos 4 caracteres'),
})

type AddUserForm = z.infer<typeof addUserFormSchema>

export default function AddUser() {
  const router = useRouter()
  const [isSubmitting, setIsSubmitting] = useState(false)

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<AddUserForm>({
    resolver: zodResolver(addUserFormSchema),
  })

  async function handleAddUser(data: AddUserForm) {
    setIsSubmitting(true)

    try {
      await api.post('/users', {
        name: data.name,
        email: data.email,
        type: data.type,
        password: data.password,
      })

      toast.success('Usuário adicionado com sucesso!')
      router.push('/dashboard')
    } catch (error) {
      toast.error('Erro ao adicionar usuário!')
      console.error(error)
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <div className="p-6 max-w-5xl mx-auto mt-10 bg-gray-950 text-white rounded-lg shadow-lg">
      <h1 className="text-2xl font-bold mb-4 text-center text-gray-100">
        Adicionar Novo Usuário
      </h1>

      <form className="space-y-4" onSubmit={handleSubmit(handleAddUser)}>
        <div className="space-y-2">
          <Label htmlFor="name">Nome</Label>
          <Input id="name" type="text" {...register('name')} />
          {errors.name && (
            <p className="text-red-500 text-sm">{errors.name.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="email">Email</Label>
          <Input id="email" type="email" {...register('email')} />
          {errors.email && (
            <p className="text-red-500 text-sm">{errors.email.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="type">Tipo</Label>
          <Input id="type" type="text" {...register('type')} />
          {errors.type && (
            <p className="text-red-500 text-sm">{errors.type.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="password">Senha</Label>
          <Input id="password" type="password" {...register('password')} />
          {errors.password && (
            <p className="text-red-500 text-sm">{errors.password.message}</p>
          )}
        </div>

        <Button
          type="submit"
          disabled={isSubmitting}
          className="w-full cursor-pointer"
        >
          {isSubmitting ? 'Adicionando...' : 'Adicionar Usuário'}
        </Button>
      </form>
    </div>
  )
}
